import React, { Component } from 'react'

class Test extends Component {
  render () {
    const { value, increment, decrement } = this.props
    return (
      <div>
        {value}
        <button onClick={increment}>+</button>
        <button onClick={decrement}>-</button>
      </div>
    )
  }
}

export default Test


